
body {
  font-family: sans-serif;
  line-height: 1.5;
  display: flex;
  justify-content: center;
  min-width: 400px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}